				</view>
			</view>
		</view>

		<view class="button-group">
			<button class="prev-btn" @click="prevPage">‰∏ä‰∏ÄÊ≠•</button>
			<button class="complete-btn" @click="submitData">ÂÆåÊàê</button>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				areaA: ['Âåó‰∫¨Â∏Ç', '‰∏äÊµ∑Â∏Ç', 'ÊµôÊ±üÁúÅ', 'Ê±üËãèÁúÅ', 'ÊπñÂåóÁúÅ', 'Âπø‰∏úÁúÅ', 'ÂêâÊûóÁúÅ', 'Â§©Ê¥•Â∏Ç'],
				areaB: ['ÂõõÂ∑ùÁúÅ', 'ÂÆâÂæΩÁúÅ', 'ÈôïË•øÁúÅ', 'ÈªëÈæôÊ±üÁúÅ', 'ÊπñÂçóÁúÅ', 'Â±±‰∏úÁúÅ', 'Á¶èÂª∫ÁúÅ', 'ËæΩÂÆÅÁúÅ', 
					'ÈáçÂ∫ÜÂ∏Ç', 'Ê≤≥ÂçóÁúÅ', 'Ê±üË•øÁúÅ', 'Â±±Ë•øÁúÅ'],
				selectedAreas: [],
				formData: {
					identity: '',
					university: '',
					englishLevel: '',
					targetMajor: '',
					targetType: '',
					targetAreas: []
				}
			}
		},
		methods: {
			toggleArea(area) {
				const index = this.selectedAreas.indexOf(area);
				if (index === -1) {
					this.selectedAreas.push(area);
				} else {
					this.selectedAreas.splice(index, 1);
				}
			},
			prevPage() {
				uni.navigateBack();
			},
			async submitData() {
				if (this.selectedAreas.length === 0) {
					uni.showToast({
						title: 'ËØ∑Ëá≥Â∞ëÈÄâÊã©‰∏Ä‰∏™ÁõÆÊ†áÁúÅ‰ªΩ',
						icon: 'none'
					});
					return;
				}

				try {
					uni.showLoading({
						title: 'ÂáÜÂ§áÂàÜÊûê...'
					});
					
					// ‰ªéÊú¨Âú∞Â≠òÂÇ®Ëé∑Âèñ‰πãÂâçÊ≠•È™§ÁöÑÊï∞ÊçÆ
					const formData = {
						identity: uni.getStorageSync('step1_data') || '',
						university: uni.getStorageSync('step2_school') || '',
						major: uni.getStorageSync('step2_major') || '',
						englishLevel: uni.getStorageSync('step3_english') || '',
						ranking: uni.getStorageSync('step3_ranking') || '',
						targetMajor: uni.getStorageSync('step4_major') || '',
						studyMode: uni.getStorageSync('step4_mode') || '',
						targetType: uni.getStorageSync('step5_target') || '',
						considerResearch: uni.getStorageSync('step5_research') || '',
						targetAreas: this.selectedAreas
					};
					
					console.log('Êèê‰∫§Êï∞ÊçÆ:', formData);
					
					// ÂÖà‰øùÂ≠òÂàÜÊûêËØ∑Ê±Ç
					const saveResult = await uniCloud.callFunction({
						name: 'saveAnalysisRequest',
						data: {
							formData: formData
						},
						timeout: 10000 // Â¢ûÂä†Ë∂ÖÊó∂Êó∂Èó¥Âà∞10Áßí
					});
					
					uni.hideLoading();
					
					if (saveResult.result && saveResult.result.code === 0) {
						const analysisId = saveResult.result.data.id;
						
						// Áõ¥Êé•Ë∑≥ËΩ¨Âà∞ÂàÜÊûê‰∏≠È°µÈù¢Ôºå‰∏çÁ≠âÂæÖÂàÜÊûêÂÆåÊàê
						uni.navigateTo({
							url: './analyzing?id=' + analysisId
						});
						
						// ÂêéÂè∞ÂºÄÂßãÂÆûÈôÖÁöÑAIÂàÜÊûêËøáÁ®ã
						uniCloud.callFunction({
							name: 'startAnalysis',
							data: { id: analysisId },
							success: (res) => {
								console.log('ÂàÜÊûêÂºÄÂßã:', res);
							},
							fail: (err) => {
								console.error('ÂêØÂä®ÂàÜÊûêÂ§±Ë¥•:', err);
							}
						});
					} else {
						throw new Error(saveResult.result?.msg || 'ÂáÜÂ§áÂàÜÊûêÂ§±Ë¥•');
					}
				} catch (e) {
					uni.hideLoading();
					uni.showModal({
						title: 'ÊèêÁ§∫',
						content: e.message || 'ÂáÜÂ§áÂàÜÊûêÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
						showCancel: false
					});
				}
			}
		}
	}
</script>

